'use client';

import { PieChart, Pie, Cell } from 'recharts';
import { Text } from '@mantine/core';

export default function DonutChart() {
  const income = 850000; 
  const expenses = 320000; 

  const data = [
    { name: 'Траты', value: expenses, color: '#d1d5db' },
    { name: 'Поступления', value: income, color: '#2563eb' }, 
  ];

  return (
    <div
      style={{
        position: 'relative',
        width: 200,
        height: 200,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <PieChart width={200} height={200}>
        <Pie
          data={data}
          cx={100}
          cy={100}
          innerRadius={65}
          outerRadius={95}
          dataKey="value"
          stroke="none"
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={entry.color} />
          ))}
        </Pie>
      </PieChart>

      <div
        style={{
          position: 'absolute',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <Text
          size="md"
          fw={700}
          style={{
            textAlign: 'center',
            color: '#111827',
          }}
        >
          {expenses.toLocaleString()} ₽
        </Text>
        <Text
          size="xs"
          style={{
            textAlign: 'center',
            color: '#9ca3af',
            marginTop: '4px',
          }}
        >
          Траты за месяц
        </Text>
      </div>
    </div>
  );
}
